<template>
  <div class="wrap">
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder=""
        @search="onSearch"
        @cancel="$router.go(-1)"
        :clearable="true"
        @clear="cl"
      />
    </form>

    <div class="box" v-show="flg">
      <h4>
        <span
          v-for="(item, index) in listData"
          :key="item.kindId"
          @click="spanCli(index, item.kindId)"
          :style="{ borderColor: num == index ? 'red' : 'transparent' }"
          >{{ item.kindTitle }}</span
        >
      </h4>
    </div>

    <haibao
      v-show="flg"
      :keyword="value"
      :page="page"
      :kid="kid"
      :tabList="tabList"
      @f="tongxin"
    ></haibao>

    <div class="ss-box" v-show="!flg">
      <h2></h2>
      <ul>
        <li
          v-for="item in hotData"
          :key="item.keyword"
          @click="say(item.keyword)"
        >
          {{ item.keyword }}
        </li>
      </ul>
      <h2></h2>
      <ul>
        <li v-for="item in history" :key="item">{{item}}</li>
      </ul>
    </div>
  </div>
</template>
<script>
import haibao from "./two/er_haibao";

export default {
  data() {
    return {
      value: "",
      flg: false,
      hotData: [],
      listData: [],
      num: 0,
      kid: 0,
      page: 1,
      tabList: [],
      history:[]
    };
  },
  components: {
    haibao,
  },
  created() {
    this.getData();
  },
  methods: {
    toast() {
      this.$toast("");
    },

    spanCli(index, kindId) {
    //   console.log(kindId);
      this.num = index;
      this.kid = kindId;
      this.biaoji = true;
      this.tabList = [];
      
    },
    tongxin(a) {
      this.tabList = a;
    },
    onSearch() {
      this.flg = true;
      this.getList();
      this.his();
      var a = [];
      if (localStorage.getItem("arr")) {
        a = localStorage.getItem("arr").split(",");
        if (a.indexOf(this.value) == -1) {
          a.push(this.value);
          localStorage.setItem("arr", a);
        }
      } else {
        a.push(this.value);
        localStorage.setItem("arr", a);
      }
    },
    cl() {
      this.flg = false;
    },
    async getList() {
      var r1 = await fetch(
        "http://192.168.204.20:9527/designkind/getDesignKindBasicInfo.do?_dataType=json",
        {
          method: "POST",
          headers: {
            "Content-type": "application/x-www-form-urlencoded",
          },
          body: "_dataClientType=3&client_type=40&type=2",
        }
      ).then((res) => res.json());
      this.listData = [
        { kindTitle: "", kindId: 0 },
        ...r1.body.data.secondLabel,
      ];
      //   console.log(r1.body.data.secondLabel);
    },
    async getData() {
      var r1 = await fetch(
        "http://192.168.204.20:9527/template/getRecommendKeyword.do?_dataType=json",
        {
          method: "POST",
          headers: {
            "Content-type": "application/x-www-form-urlencoded",
          },
          body: "_dataClientType=3&client_type=40&from=1",
        }
      ).then((res) => res.json());
      this.hotData = r1.body.data;
      
    },
    his() {
      if (localStorage.getItem("arr")) {
        var a = localStorage.getItem("arr").split(",");
        this.history = a;
      }
    },
    say(b) {
      this.value = b;
      this.onSearch();
      this.his();
      var a = [];
      if (localStorage.getItem("arr")) {
        a = localStorage.getItem("arr").split(",");
        if (a.indexOf(this.value) == -1) {
          a.push(this.value);
          localStorage.setItem("arr", a);
        }
      } else {
        a.push(this.value);
        localStorage.setItem("arr", a);
      }
    },
  },
  watch: {
    tabList() {
    //   console.log("tabList");
    //   console.log(this.tabList);
    },
  },
};
</script>

<style scoped lang=scss>
.wrap {
  background-color: white;
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}
.box {
  h4 {
    width: 100%;
    overflow: auto;
    white-space: nowrap;
    line-height: 0.5rem;
    font-size: 0;
    border-bottom: 2px solid #eee;
    span {
      margin: 0.3rem;
      font-size: 0.36rem;
      padding: 0.2rem 0;
      display: inline-block;
      border-bottom: 2px solid transparent;
    }
  }
}
.ss-box {
  h2 {
    font-size: 0.46rem;
    padding: 0.3rem;
    box-sizing: border-box;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      margin: 0.2rem 0.1rem;
      font-size: 0.3rem;
      background-color: #f3f4f9;
      width: 1.5rem;
      line-height: 0.6rem;
      text-align: center;
      border-radius: 0.5rem;
      color: rgba(0, 0, 0, 0.6);
      padding: 0.1rem;
    }
  }
}
</style>
