<template>
  <div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <ul>
        <li>
          <div v-if="pbList.length">
            <div
              class="layImg"
              v-for="(item, index) in pbList[0].imgUrl"
              :key="index"
            >
              <img
                v-lazy="item.img"
                alt=""
                :style="{ height: (item.height / item.width) * 4.5 + 'rem' }"
              />
            </div>
          </div>
        </li>
        <li>
          <div v-if="pbList.length">
            <div
              class="layImg"
              v-for="(item, index) in pbList[1].imgUrl"
              :key="index"
            >
              <img
                v-lazy="item.img"
                alt=""
                :style="{ height: (item.height / item.width) * 4.5 + 'rem' }"
              />
            </div>
          </div>
        </li>
      </ul>
    </van-list>
  </div>
</template>
<script>
export default {
  props: ["keyword", "page", "kid", "tabList"],
  data() {
    return {
      list: [],
      pbList: [],
      flag: true,
      page_No: 1,
      loading: false,
      finished: false,
    };
  },
  created() {
    this.getData();
    this.page_No = this.page;
  },
  methods: {
    onLoad() {
      this.page_No++;
      this.getData();
    },
    async getData() {
      var r1 = await fetch(
        "http://192.168.204.20:9527/template/wxSmallAppSearch.do?_dataType=json",
        {
          method: "POST",
          headers: {
            "Content-type": "application/x-www-form-urlencoded",
          },
          body:
            "_dataClientType=3&client_type=40&pageNo=" +
            this.page_No +
            "&pageSize=20&kw=" +
            this.keyword +
            "&second_kind_id=" +
            this.kid +
            "&time_order=0&use_times_order=0&collection_times_order=0",
        }
      ).then((res) => res.json());
        // console.log(r1.body)
      this.list = r1.body.templates;
      this.loading = false;

      if (this.pbList.length == 0) {
        for (var i = 0; i < 2; i++) {
          this.pbList.push({
            h: 0,
            imgUrl: [],
          });
        }
      }

      for (var j = 0; j < this.list.length; j++) {
        this.pbList.sort((a, b) => (a.h > b.h ? 1 : -1));
        let s =
          "http:" +
          this.list[j].designTemplateThumbUrls[0] +
          "?x-oss-process=image/resize,w_300/format,jpg";
        let h = parseInt(this.list[j].height);
        let w = parseInt(this.list[j].width);
        this.pbList[0].h += h + 10; //
        let id = this.list[j].designTemplateId;

        this.pbList[0].imgUrl.push({
          img: s,
          width: w,
          height: h,
          designTemplateId: id,
        });
      }
      if (this.pbList[0].imgUrl.length < 10) {
        this.finished = true;
      } else {
        this.finished = false;
      }

      if (this.pbList[0].imgUrl.length == 0) {
        this.finished = true;
      } else {
        this.finished = false;
      }
    },
  },
  watch: {
    keyword() {
      this.getData();
      this.page_No = this.page;
      this.pbList = [];
    },

    kid() {
      this.getData();
      this.page_No = this.page;
      this.pbList = [];
    },
    pbList: {
      deep: true,
      handler() {
        this.$emit("f", this.pbList);
        // console.log(this.list);
        if (this.pbList.length) {
          if (this.pbList[0].imgUrl.length < 10) {
            this.finished = true;
          } else {
            this.finished = false;
          }
        }
      },
    },
  },
};
</script>
<style scoped lang="scss">
.layImg {
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url(https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2Fb75fc5b61441db0de8f3caeba20275e8107d270811b329-QsLrXX_fw658&refer=http%3A%2F%2Fhbimg.b0.upaiyun.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1620399802&t=6566604ee3886067d714dc8d479f759d);
  margin: 0.2rem 0;
}
ul {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  li {
    width: 4.5rem;
    img {
      display: block;
      width: 100%;
    }
  }
}
</style>